import axios from "axios";

const url = '/api/about'

export default class Berita {

    // Get all
    static async all() {
        const res = await axios.get(url)
        return res.data
    }

    // Update
    static async update(id, form) {
        const res = await axios.patch(url + '/' + id, form)
        return res.data
    }
}